<template>
  <div id="loading-container" class="vs-con-loading__container">
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card>
      <form-wizard ref="wizard"
                   color="rgba(var(--vs-primary), 1)"
                   :title="null" :subtitle="null"
                   :nextButtonText="$t('NEXT')"
                   :backButtonText="$t('PREVIOUS')">

        <template slot="custom-buttons-left" slot-scope="props">
          <vs-button class="mr-2" color="primary" type="border" @click="cancel" v-if="props.activeTabIndex === 0">{{ $t('CANCEL') }}</vs-button>
        </template>
        <template slot="finish">
          <div class="dropdown-button-container">
            <vs-button class="btnx" type="filled" @click="createReport('browse')">{{ $t('SUBMIT') }}</vs-button>
            <vs-dropdown vs-trigger-click>
              <vs-button class="btn-drop" type="filled" icon="fa-angle-down" icon-pack="far"></vs-button>
              <vs-dropdown-menu>
                <vs-dropdown-item @click="createReport('stay')">
                  <span>{{ $t('SUBMIT_STAY') }}</span>
                </vs-dropdown-item>
                <vs-dropdown-item @click="createReport('view')">
                  <span>{{ $t('SUBMIT_VIEW') }}</span>
                </vs-dropdown-item>
              </vs-dropdown-menu>
            </vs-dropdown>
          </div>
        </template>

        <tab-content :title="$t('GENERAL')" icon="far fa-file-alt">
          <div class="nv-row mb-4">
            <div class="nv-col w-full required">
              <label class="vs-input--label">{{ $t('REPORT_NAME') }}</label>
              <vs-input class="w-full" v-model="reportName"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('TYPE') }}</label>
              <vs-select autocomplete class="w-full" :placeholder="$t('SELECT_VALUE')" v-model="reportTypeEnumId" @change="selectType">
                <vs-select-item v-for="(enumeration, index) in reportTypes" :key="index" :value="enumeration.enumId" :text="enumeration.description"/>
              </vs-select>
            </div>
            <div class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('CATEGORY') }}</label>
              <report-category-lookup :title="$t('CATEGORY')" valueField="categoryId" displayField="categoryName" @select-record="selectCategoryRecord"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('PSEUDO_ID') }}</label>
              <vs-input class="w-full" v-model="pseudoId"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('ICON_CLASS') }} <a href="https://fontawesome.com/icons?d=gallery" target="_blank"><i class="far fa-question-circle"></i></a></label>
              <vs-input class="w-full" v-model="iconClass"/>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <vs-checkbox v-model="disabled">{{ $t('DISABLED') }}</vs-checkbox>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('DATA')" icon="far fa-database">
          <div class="nv-row mb-4">
            <div v-if="reportTypeEnumId === 'RPT_TYPE_DATA_DOCUMENT'" class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('DATA_DOCUMENT_ID') }}</label>
              <vs-input class="w-full" v-model="dataDocumentId"/>
            </div>
            <div v-else class="nv-col sm:w-1/2 w-full required">
              <label class="vs-input--label">{{ $t('SERVICE_NAME') }}</label>
              <vs-input class="w-full" v-model="serviceName"/>
            </div>
            <div class="nv-col sm:w-1/2 w-full">
              <label class="vs-input--label">{{ $t('DATA_FIELD') }}</label>
              <vs-input class="w-full" v-model="dataField" :disabled="reportTypeEnumId !== 'RPT_TYPE_SERVICE_INLINE'"/>
            </div>
          </div>
        </tab-content>

        <tab-content :title="$t('NOTES')" icon="far fa-sticky-note">
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('BROWSE_NOTES') }}</label>
              <div class="code-editor">
                <codemirror v-model="browseNotes" :options="vueOptions"></codemirror>
              </div>
            </div>
          </div>
          <div class="nv-row mb-4">
            <div class="nv-col w-full">
              <label class="vs-input--label">{{ $t('RUN_NOTES') }}</label>
              <div class="code-editor">
                <codemirror v-model="runNotes" :options="vueOptions"></codemirror>
              </div>
            </div>
          </div>
        </tab-content>
      </form-wizard>
    </nv-card>
  </div>
</template>

<script>
import AlertMixin from '@/mixins/commons/AlertMixin'
import CodeEditorMixin from '@/mixins/commons/CodeEditorMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ReportCategoryLookup from '@/views/report/report-category/ReportCategoryLookup'
import ReportService from '@/services/report/ReportService'
import EnumerationService from '@/services/commons/EnumerationService'
import { FormWizard, TabContent } from 'vue-form-wizard'

export default {
  name: 'report-create',
  mixins: [AlertMixin, CodeEditorMixin, LoadingMixin],
  components: {
    FormWizard,
    TabContent,
    ReportCategoryLookup
  },
  data: function () {
    return {
      reportName: '',
      reportTypeEnumId: 'RPT_TYPE_SERVICE_FILE',
      reportTypes: [],
      pseudoId: '',
      categoryId: '',
      iconClass: '',
      disabled: '',
      serviceName: '',
      dataDocumentId: '',
      dataField: '',
      browseNotes: '',
      runNotes: ''
    }
  },
  methods: {
    async load () {
      let self = this
      EnumerationService.enumerations({
        enumTypeId: 'ReportType',
        orderByField: 'sequenceNum,description'
      }).then(function (response) {
        self.reportTypes = response.data.enumerationList || []
      })
    },
    selectCategoryRecord (record) {
      this.categoryId = record.categoryId || ''
    },
    selectType () {
      this.dataField = (this.reportTypeEnumId === 'RPT_TYPE_DATA_DOCUMENT') ? 'documents' : ''
    },
    createReport (nextAction) {
      let self = this
      this.startLoading('loading-container')
      ReportService.createReport({
        reportName: this.reportName,
        reportTypeEnumId: this.reportTypeEnumId,
        pseudoId: this.pseudoId,
        categoryId: this.categoryId,
        iconClass: this.iconClass,
        disabled: this.disabled ? 'Y' : 'N',
        serviceName: this.serviceName,
        dataDocumentId: this.dataDocumentId,
        dataField: this.dataField,
        browseNotes: this.browseNotes,
        runNotes: this.runNotes
      }).then(function (response) {
        self.stopLoading('loading-container')
        if (response.status === 200) {
          if (nextAction === 'stay') {
            self.showSuccessAlert(response.data.messages)
            self.$refs.wizard.reset()
          } else if (nextAction === 'view') {
            self.$router.push({ name: 'ViewReport', params: { id: response.data.reportId } })
          } else {
            self.$router.push({ name: 'BrowseReportDefinitions' })
          }
        }
      }).catch(function (error) {
        self.stopLoading('loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted: function () {
    this.load()
  }
}
</script>
